import React, { Component } from "react";
import { Provider, connect } from "react-redux";
import { withRouter } from "react-router";
import { message, Alert } from "antd";
import CodeSnippet from "@/components/code-snippet";
import store from "@/store";
import { home as trackEvents } from "@/tracks";
import $log from "@/utils/logger";
import { track, inject } from "../../";

const UVPVTONPSnippet = `
@inject({ trackEvents })
class Home extends Component {
  ...
  @track("UVPV")
  @track("TONP")
  componentDidMount() {
    ...
  }
  ...
}
`;

const ASYNCDELAYSnippet = `
@inject({ trackEvents })
class Home extends Component {
  state = {
    content: null
  };

  @track("async.delay", 22122, { delay: 3000 })
  initContent = async val => {
    const response = await new Promise(resolve => {
      setTimeout(() => {
        resolve({ data: "success" });
      }, 300);
    });

    this.setState({
      content: response.data
    });
  };
  ...
}
`;

function mapStateToProps(state) {
  return {
    userInfo: state.userInfo
  };
}

@withRouter
@connect(mapStateToProps)
@inject({ trackEvents })
class Trigger extends Component {
  state = {
    content: null
  };

  @track("UVPV")
  @track("TONP")
  componentDidMount() {
    message.success("UPPV");
    this.initContent("3s");
  }

  @track("async.delay", 22122, { delay: 3000 })
  initContent = async val => {
    const response = await new Promise(resolve => {
      setTimeout(() => {
        resolve({ data: "success" });
      }, 300);
    });

    $log("initContent ", val);
    this.setState({
      content: response.data
    });
  };

  componentWillUnmount() {
    message.success("TONP");
  }

  render() {
    return (
      <div className="r-track">
        <p className="description">
          UVPVTONP
        </p>
        <CodeSnippet code={UVPVTONPSnippet} lang="js"></CodeSnippet>
        <Alert
          message="UVPVTONP componentDidMount"
          type="warning"
        />
        <p className="description">
          3sdelay
        </p>
        <CodeSnippet code={ASYNCDELAYSnippet} lang="js"></CodeSnippet>
        <Alert
          message="async.delay  class component "
          type="warning"
        />
      </div>
    );
  }
}

export default class WithTrigger extends Component {
  render() {
    return (
      <Provider store={store}>
        <Trigger />
      </Provider>
    );
  }
}
