import trackAction from "../action";

export default {
  /**
   * UPPV
   */
  UVPV() {
    trackAction("1,3");
  },
  /**
   * Time on Page
   * @param {String} stt 
   */
  TONP(_, { stt }) {
    trackAction("2", { stt });
  },
  22120({ userInfo }) {
    trackAction("22120", {
      userInfo: JSON.stringify(userInfo)
    });
  },
  22121(context, val) {
    trackAction("22121", {
      tamp: context.date,
      param: val
    });
  },
  22122(context, val) {
    trackAction("22122", {
      content: context.content,
      param: val
    });
  },
  22123(_, val) {
    trackAction("22123", {
      param: val
    });
  }
};
