import React, { Component } from "react";
import { Provider, connect } from "react-redux";
import { withRouter } from "react-router";
import { Button, message, Alert } from "antd";
import CodeSnippet from "@/components/code-snippet";
import store from "@/store";
import { home as trackEvents } from "@/tracks";
import $log from "@/utils/logger";
import { track, inject } from "../../";

const triggerSnippet = `
@inject({ trackEvents })
class Home extends Component {
  state = {
    date: Date.now(),
  };

  @track("trigger", 22121)
  handleClick(val, e) {
    this.setState({ date: Date.now() });
  }

  render() {
    return (
      <button onClick={this.handleClick.bind(this, "button")}>Click Me</button>
    );
  }
}
`;

const triggerAfterSnippet = `
@inject({ trackEvents })
class Home extends Component {
  state = {
    date: Date.now(),
  };

  @track("trigger.after", 22121)
  handleClick(val, e) {
    this.setState({ date: Date.now() });
  }

  render() {
    return (
      <button onClick={this.handleClick.bind(this, "button")}>Click Me</button>
    );
  }
}
`;

const triggerAsyncSnippet = `
@inject({ trackEvents })
class Home extends Component {
  state = {
    date: Date.now(),
  };

  @track("async", 22121)
  handleClick(val, e) {
    const response = await new Promise(resolve => {
      setTimeout(() => {
        resolve({ date: Date.now() });
      }, 300);
    });

    this.setState({ date: response.date });
  }

  render() {
    return (
      <button onClick={this.handleClick.bind(this, "button")}>Click Me</button>
    );
  }
}
`;

const triggerAsyncActionSnippet = `
@inject({ trackEvents })
class Home extends Component {
  ...
  @track("async", 22120)
  getUserInfo = async () => {
    await this.props.dispatch(fetchUserInfo());
  };
  ...
}
`;

const triggerAsyncOnceSnippet = `
@inject({ trackEvents })
class Home extends Component {
  state = {
    date: Date.now(),
  };

  @track("async.once", 22121)
  handleClick(val, e) {
    const response = await new Promise(resolve => {
      setTimeout(() => {
        resolve({ date: Date.now() });
      }, 300);
    });

    this.setState({ date: response.date });
  }

  render() {
    return (
      <button onClick={this.handleClick.bind(this, "button")}>Click Me</button>
    );
  }
}
`;

function mapStateToProps(state) {
  return {
    userInfo: state.userInfo
  };
}

@withRouter
@connect(mapStateToProps)
@inject({ trackEvents })
class Trigger extends Component {
  constructor(props) {
    super(props);
    this.buttonRef = null;
    this.state = {
      date: null,
      target: null,
      content: null
    };
  }

  @track("trigger", 22121)
  handleClick(val, e) {
    $log("handleClick ", val, e.target);
    message.success("handleClick ");
    this.setState({
      date: Date.now(),
      target: e.target
    });
  }

  @track("trigger.after", 22121)
  handleClick2(val, e) {
    $log("handleClick ", val, e.target);
    message.success("handleClick ");
    this.setState({
      date: Date.now(),
      target: e.target
    });
  }

  // @track("async.once", 22121)
  @track("async", 22121)
  handleClick3 = async (val, e) => {
    e.persist();
    const response = await new Promise(resolve => {
      setTimeout(() => {
        resolve({ date: Date.now() });
      }, 300);
    });
    $log(
      " handleClick ",
      val,
      e.target,
      response.date
    );
    message.success(" handleClick ");
    this.setState({
      date: response.date,
      target: e.target
    });
  };

  @track("async.once", 22121)
  handleClick4 = async (val, e) => {
    e.persist();
    const response = await new Promise(resolve => {
      setTimeout(() => {
        resolve({ date: Date.now() });
      }, 300);
    });
    $log(
      " handleClick ",
      val,
      e.target,
      response.date
    );
    message.success(" handleClick ");
    this.setState({
      date: response.date,
      target: e.target
    });
  };

  render() {
    return (
      <div className="r-track">
        <p className="description">
          buttonid22121
        </p>
        <Button
          type="primary"
          onClick={this.handleClick.bind(this, "first button")}
        >
          Click Me
        </Button>
        <CodeSnippet code={triggerSnippet} lang="js"></CodeSnippet>
        <p className="description">
          buttonafter
        </p>
        <Button
          type="primary"
          onClick={this.handleClick2.bind(this, "second button")}
        >
          Click Me
        </Button>
        <CodeSnippet code={triggerAfterSnippet} lang="js"></CodeSnippet>
        <Alert
          message="after actionstatestatestate"
          type="warning"
        />
        <p className="description">
          buttonasync
        </p>
        <Button
          type="primary"
          onClick={this.handleClick3.bind(this, "third button")}
        >
          Click Me
        </Button>
        <CodeSnippet code={triggerAsyncSnippet} lang="js"></CodeSnippet>
        <p className="description">
           redux  action
          async/await
        </p>
        <CodeSnippet code={triggerAsyncActionSnippet} lang="js"></CodeSnippet>
        <p className="description">
          once
        </p>
        <Button
          type="primary"
          onClick={this.handleClick4.bind(this, "fourth button")}
        >
          Click Me
        </Button>
        <CodeSnippet code={triggerAsyncOnceSnippet} lang="js"></CodeSnippet>
      </div>
    );
  }
}

export default class WithTrigger extends Component {
  render() {
    return (
      <Provider store={store}>
        <Trigger />
      </Provider>
    );
  }
}
